/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
header,
div,
footer,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  max-height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
}

body {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 320px;
  z-index: 1;
  overflow: hidden;
}

.container {
  padding: 0px 10px;
}

body::after {
  /* Ellipse 1 */
  content: "";
  display: block;
  position: absolute;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  left: 10px;
  left: calc(50% - 150px);
  top: 139px;
  /* Light */
  background: #ffffff;
  box-shadow: 0px 80px 100px rgba(239, 127, 26, 0.2);
  transform: rotate(-180deg);
  z-index: -2;
}
@media (min-width: 640px) {
  body::after {
    width: 498px;
    height: 498px;
    left: calc(50% - 249px);
    top: min(190px, 50% - 249px + 66px);
  }
}
@media (min-width: 993px) {
  body::after {
    width: 690px;
    height: 690px;
    left: calc(50% - 345px);
    top: min(180px, 50% - 249px);
  }
}
@media (min-width: 1200px) {
  body::after {
    width: 690px;
    height: 690px;
    left: calc(50% - 345px);
    top: min(190px, 50% - 345px + 75px);
  }
}

button {
  cursor: pointer;
  position: relative;
  border: 1px solid #282828;
  background: none;
  width: 128px;
  height: 45px;
  padding: 0;
  font-family: "Fira Sans Medium";
  color: #FFFFFF;
}

button::after {
  content: "";
  display: block;
  width: 128px;
  height: 45px;
  background: #0077A0;
  position: absolute;
  top: 4px;
  left: 4px;
  z-index: -1;
}

button:hover {
  background-color: #EF7F1A;
}

button:hover:after {
  display: none;
}

@media (min-width: 640px) {
  .container {
    padding: 0px 20px;
  }
}
@media (min-width: 1200px) {
  .container {
    padding: 0px 60px;
  }
}
@font-face {
  font-family: "Helvetica Neue";
  src: url("../fonts/HelveticaNeueCyr-Bold.woff") format("woff"), url("../fonts/HelveticaNeueCyr-Bold.ttf") format("ttf"), url("../fonts/HelveticaNeueCyr-Bold.eot") format("eot");
}
@font-face {
  font-family: "Fira Sans Medium";
  src: url("../fonts/FiraSans-Medium.woff2") format("woff2"), url("../fonts/FiraSans-Medium.woff") format("woff"), url("../fonts/FiraSans-Medium.ttf") format("ttf"), url("../fonts/FiraSans-Medium.eot") format("eot");
}
@font-face {
  font-family: "Fira Sans Regular";
  src: url("../fonts/FiraSans-Regular.woff2") format("woff2"), url("../fonts/FiraSans-Regular.woff") format("woff"), url("../fonts/FiraSans-Regular.ttf") format("ttf"), url("../fonts/FiraSans-Regular.eot") format("eot");
}
.page-header {
  position: sticky;
  display: flex;
  justify-content: space-between;
  align-items: end;
  width: 100%;
  height: 72px;
  min-height: 72px;
}
.page-header button {
  width: 116px;
  height: 42px;
  font-size: 18px;
  line-height: 21.6px;
  letter-spacing: 0.05em;
  padding-left: 0px;
  padding-right: 3px;
}
.page-header button::after {
  width: 116px;
  height: 42px;
  top: 6px;
  left: 6px;
}

.page-header__auth {
  display: none;
  align-items: center;
  padding-right: 7px;
  padding-bottom: 5px;
}
.page-header__auth a {
  text-decoration: none;
}

.page-header__login {
  font-family: "Fira Sans Medium";
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.05em;
  color: #282828;
  padding-right: 52px;
}

.page-header__logo {
  padding-top: 0px;
}

.page-header__user {
  padding-bottom: 11px;
}

@media (min-width: 640px) {
  .page-header {
    height: 97px;
    min-height: 97px;
  }
  .page-header button {
    padding-top: 1px;
    padding-right: 5px;
  }
  .page-header__user {
    display: none;
  }
  .page-header__auth {
    display: flex;
  }
}
@media (min-width: 993px) {
  .page-header {
    height: 98px;
    min-height: 98px;
  }
}
.page-main {
  padding-top: 88px;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: auto;
}

.page-main__title {
  font-family: "Helvetica Neue", Helvetica, sans-serif;
  font-size: 50px;
  line-height: 60px;
  letter-spacing: 0.25em;
  text-transform: uppercase;
  text-align: center;
  padding-left: 0.25em;
}

.page-main__tagline {
  font-family: "Fira Sans Medium";
  font-size: 19px;
  line-height: 23px;
  letter-spacing: 0.11em;
  padding-right: 0.11em;
  padding-top: 39px;
}

.email-form {
  padding-top: 36px;
  padding-left: 4px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.email-form input {
  outline: none;
  border: 1px solid #282828;
  border-right: none;
  padding-left: 9px;
  width: 167px;
  height: 45px;
}
.email-form input::placeholder {
  color: #6F6F6F;
  font-family: "Fira Sans Regular";
  font-size: 12px;
  letter-spacing: 0.05em;
  line-height: 14.4px;
}
.email-form button {
  font-size: 16px;
  letter-spacing: 0.05em;
  padding-left: 8px;
}

@media (min-width: 640px) {
  .page-main {
    padding-top: 208px;
  }
  .page-main__title {
    font-size: 60px;
    line-height: 60px;
    padding-left: 0.22em;
  }
  .page-main__tagline {
    padding-top: 44px;
    padding-right: 12px;
    font-size: 26px;
    line-height: 31px;
  }
  .email-form {
    padding-top: 26px;
    padding-left: 15px;
  }
  .email-form input {
    padding-top: 5px;
    width: 266px;
    padding-left: 10px;
  }
  .email-form input::placeholder {
    font-size: 12px;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 0.05em;
    padding-left: 0.05em;
  }
  .email-form button {
    width: 140px;
    padding-left: 0px;
    padding-right: 2px;
  }
  .email-form button::after {
    width: 140px;
    top: 6px;
    left: 6px;
  }
}
@media (min-width: 640px) and (min-width: 993px) {
  .page-main {
    padding-top: 283px;
  }
  .page-main__title {
    font-size: 80px;
    line-height: 80px;
    padding-left: calc(0.22em + 1px);
  }
  .page-main__tagline {
    padding-top: 64px;
  }
  .email-form {
    padding-left: 14px;
  }
}
@media (min-width: 640px) and (min-width: 1200px) {
  .email-form {
    padding-left: 54px;
  }
}
.page-footer {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 60px;
  margin-top: 20px;
}
@media (min-width: 640px) {
  .page-footer {
    margin-bottom: 50px;
    justify-content: end;
  }
}

.page-footer__icons {
  height: 32px;
  width: 149px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}