.page-footer {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 60px;
  margin-top: 20px;
  
  @media (min-width: $tablet-min-width) {
    margin-bottom: 50px;
    justify-content: end;
  }
}

.page-footer__icons {
  height: 32px;
  width: 149px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
